import Vue from 'vue'
import Router from 'vue-router'
import Photo from '@/components/Photo'
import GIF1 from '@/components/GIF1'
import Content from '@/components/Content'
import index from '@/components/index'
import Time from '@/components/Time'
import tag1 from '@/components/tag_1'
import tag2 from '@/components/tag_2'
import tag3 from '@/components/tag_3'
import tag4 from '@/components/tag_4'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'index',
      components: {
        photo: index,
        time: Time
      }
    },
    {
      path: '/video',
      name: 'video',
      components: {
        photo: Photo,
        content: Content,
        time: Time
      }
    },
    {
      path: '/GIF1',
      name: 'GIF1',
      components: {
        photo: GIF1,
        time: Time,
        content: tag1
      }
    },
    {
      path: '/GIF2',
      name: 'GIF2',
      components: {
        photo: GIF1,
        time: Time,
        content: tag2
      }
    },
    {
      path: '/GIF3',
      name: 'GIF3',
      components: {
        photo: GIF1,
        time: Time,
        content: tag3
      }
    },
    {
      path: '/GIF4',
      name: 'GIF4',
      components: {
        photo: GIF1,
        time: Time,
        content: tag4
      }
    }

    // {
    //   path: '/about',
    //   name: 'about',
    //   components: {
    //     header:Header,
    //     home:About
    //   }
    // }
  ]
})



